.social-main{
    margin-top: 40px;
    padding: 15px 0;
    background-color: #1266F1;
    display: flex;
    justify-content: space-between;
    align-items: center;
}
#social-cont{
    display: flex;
    justify-content: space-between;
    color: white;
}
.social-icons{
    display: flex;
}
.social-icons li{
    padding: 0 10px;
}
.social-icons li a{
    color: white;
}